import React from 'react';
import styled from 'styled-components';
import Pencil from '@splunk/react-icons/Pencil';
import { _ } from '@splunk/ui-utils/i18n';
import Button from '@splunk/react-ui/Button';
import ScreenReaderContent from '@splunk/react-ui/ScreenReaderContent';
import { variables } from '@splunk/themes';

// eslint-disable-next-line react-perf/jsx-no-new-object-as-prop
const revertPointerEvents: React.CSSProperties = { pointerEvents: 'initial' };

const pencilIcon = (
    <Pencil
        data-test="edit-icon"
        aria-hidden={false}
        style={revertPointerEvents}
    />
);

export interface EditButtonProps {
    onEditClick?: React.MouseEventHandler;
}

const StyledButton = styled(Button)`
    width: ${variables.inputHeight};
    flex-grow: 0;
`;

export const EditButton = ({ onEditClick }: EditButtonProps) => {
    if (!onEditClick) {
        return null;
    }

    return (
        <StyledButton
            data-test="item-edit"
            appearance="subtle"
            icon={pencilIcon}
            onClick={onEditClick}
        >
            <ScreenReaderContent>{_('Edit')}</ScreenReaderContent>
        </StyledButton>
    );
};
