import React from 'react';
import TrashCanCross from '@splunk/react-icons/TrashCanCross';
import { _ } from '@splunk/ui-utils/i18n';
import Button, { type ButtonPropsBase } from '@splunk/react-ui/Button';
import ScreenReaderContent from '@splunk/react-ui/ScreenReaderContent';
import { variables } from '@splunk/themes';
import styled from 'styled-components';

// eslint-disable-next-line react-perf/jsx-no-new-object-as-prop
const revertPointerEvents: React.CSSProperties = { pointerEvents: 'initial' };

const removeIcon = (
    <TrashCanCross
        data-test="remove-icon"
        aria-hidden={false}
        style={revertPointerEvents}
    />
);

export interface RemoveButtonProps {
    onRemoveClick?: React.MouseEventHandler;
    RemoveIcon?: ButtonPropsBase['icon'];
}

const StyledButton = styled(Button)`
    width: ${variables.inputHeight};
    flex-grow: 0;
`;

export const RemoveButton = ({
    onRemoveClick,
    // This icon was originally bundled with its own ScreenReaderContent to allow customization of the icon text
    // if modification of the screen reader text is still needed, we'll need to add another prop
    RemoveIcon = removeIcon,
}: RemoveButtonProps) => {
    if (!onRemoveClick) {
        return null;
    }

    return (
        <StyledButton
            data-test="item-remove"
            appearance="subtle"
            icon={RemoveIcon}
            onClick={onRemoveClick}
        >
            <ScreenReaderContent>{_('Remove')}</ScreenReaderContent>
        </StyledButton>
    );
};
