import React from 'react';
import styled from 'styled-components';
import { variables } from '@splunk/themes';
import { RemoveButton, type RemoveButtonProps } from './RemoveButton';
import { EditButton, type EditButtonProps } from './EditButton';
import { ItemName, type ItemNameProps } from './ItemName';
import {
    VisibilityButton,
    type VisibilityButtonProps,
} from './VisibilityButton';

interface ItemProps {
    dataTest?: string;
    dataTestId?: string;
    isItemClickable?: boolean;
}

const Item = styled.div.attrs<ItemProps>(({ dataTest, dataTestId }) => ({
    'data-test': dataTest,
    'data-test-id': dataTestId,
}))<ItemProps>`
    flex-grow: 1;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    color: ${variables.contentColorDefault};
    line-height: ${variables.inputHeight};
    height: ${variables.inputHeight};
    &:hover {
        cursor: ${(prop) => (prop.isItemClickable ? 'pointer' : 'default')};
    }
`;

export interface EditableListItemProps
    extends ItemNameProps,
        EditButtonProps,
        RemoveButtonProps,
        VisibilityButtonProps {
    'data-test'?: string;
    'data-test-id'?: string;
}

export const EditableListItem = ({
    name,
    visibilityState,
    onToggleVisibility,
    onItemClick,
    onEditClick,
    onRemoveClick,
    RemoveIcon,
    'data-test': dataTest = 'editable-list-item',
    'data-test-id': dataTestId,
    isUsed = true,
}: EditableListItemProps) => {
    const hasVisibilityToggle = !!visibilityState && !!onToggleVisibility;

    return (
        <Item dataTest={dataTest} dataTestId={dataTestId}>
            <ItemName
                name={name}
                isUsed={isUsed}
                hasVisibilityToggle={hasVisibilityToggle}
                onItemClick={onItemClick}
            />
            <EditButton onEditClick={onEditClick} />
            <VisibilityButton
                visibilityState={visibilityState}
                onToggleVisibility={onToggleVisibility}
            />
            <RemoveButton
                onRemoveClick={onRemoveClick}
                RemoveIcon={RemoveIcon}
            />
        </Item>
    );
};
